$("#telephone").intlTelInput({

// whether or not to allow the dropdown
allowDropdown: true,

// if there is just a dial code in the input: remove it on blur, and re-add it on focus

autoHideDialCode: true,

// add a placeholder in the input with an example number for the selected country

autoPlaceholder: "polite",

// modify the auto placeholder

customPlaceholder: "",
 
// append menu to specified element

dropdownContainer: null,

// don't display these countries

excludeCountries: [],

// format the input value during initialisation and on setNumber

formatOnDisplay: true,

// geoIp lookup function

geoIpLookup: null,

// inject a hidden input with this name, and on submit, populate it with the result of getNumber

hiddenInput: "",

// initial country

initialCountry: "ua",

// localized country names e.g. { 'de': 'Deutschland' }

localizedCountries: null,

// don't insert international dial codes

nationalMode: true,

// display only these countries

onlyCountries: ["ua","ru"],

// number type to use for placeholders

placeholderNumberType: "MOBILE",

// the countries at the top of the list. defaults to united states and united kingdom

preferredCountries: [],

// display the country dial code next to the selected flag so it's not part of the typed number

separateDialCode: false,

// specify the path to the libphonenumber script to enable validation/formatting

utilsScript: "build/js/utils.js"

});

var second = 1000,
      minute = second * 60,
      hour = minute * 60,
      day = hour * 24;

var countDown = new Date('March 30, 2019 00:00:00').getTime(),
    x = setInterval(function() {

      var now = new Date().getTime(),
          distance = countDown - now;

      // document.getElementById('days').innerText = Math.floor(distance / (day)),
        document.getElementById('hours').innerText = Math.floor((distance % (day)) / (hour)),
        document.getElementById('minutes').innerText = Math.floor((distance % (hour)) / (minute)),
        document.getElementById('seconds').innerText = Math.floor((distance % (minute)) / second);
      
      //do something later when date is reached
      //if (distance < 0) {
      //  clearInterval(x);
      //  'IT'S MY BIRTHDAY!;
      //}

    }, second)


$(document).ready(function() {
$(".go-to").on("click", function(e) {
        e.preventDefault();
        var t = $(this).attr("href");
        if ($(t).length) {
            var s = $(t).offset().top;
            $("body,html").stop().animate({
                scrollTop: s
            }, 1000)
        } else console.warn("ID don't search!")
	}),
$('input#go').click( function(event){ // лoвим клик пo ссылки с id="go"
		event.preventDefault(); // выключaем стaндaртную рoль элементa
		$('#overlay').fadeIn(400, // снaчaлa плaвнo пoкaзывaем темную пoдлoжку
		 	function(){ // пoсле выпoлнения предъидущей aнимaции
				$('#modal_form') 
					.css('display', 'block') // убирaем у мoдaльнoгo oкнa display: none;
					.animate({opacity: 1, top: '25%'}, 200); // плaвнo прибaвляем прoзрaчнoсть oднoвременнo сo съезжaнием вниз
		});
	});
	/* Зaкрытие мoдaльнoгo oкнa, тут делaем тo же сaмoе нo в oбрaтнoм пoрядке */
	$('.wrap_close, #overlay').click( function(){ // лoвим клик пo крестику или пoдлoжке
		$('#modal_form')
			.animate({opacity: 0, top: '45%'}, 200,  // плaвнo меняем прoзрaчнoсть нa 0 и oднoвременнo двигaем oкнo вверх
				function(){ // пoсле aнимaции
					$(this).css('display', 'none'); // делaем ему display: none;
					$('#overlay').fadeOut(400); // скрывaем пoдлoжку
				}
			);
	});


})